/* eslint-disable react/react-in-jsx-scope -- Unaware of jsxImportSource */
/** @jsxImportSource @emotion/react */

import React, { useState } from "react";
import MouseDown from "../MouseDown";
import {
  Stack,
  Typography,
  IconButton,
  Container,
  Box,
} from "@mui/material";
import PlayCircleFilledIcon from "@mui/icons-material/PlayCircleFilled";
import filler from "../../assets/images/section04.png";
import { Parallax } from "react-scroll-parallax";
import Media from "../Media";
import { css } from "@emotion/react";
import Lamp from "../Lamp";

const Hero: React.FC = () => {
  return (
    <Container maxWidth="lg" sx={{ flexGrow: 1, position: "relative" }}>
      <Stack
        direction={{ md: "row", xs: "column-reverse" }}
        justifyContent={{ xs: "center" }}
        alignItems={{ xs: "center" }}
      >
        <Box width={{ md: "35%" }}>
          <Parallax speed={10} rotate={[-30, 30]}>
            <Box>
              <Media
                src={filler}
                alt=""
                saturate
                sx={{ width: { xs: "80%", md: "100%" }, top: { md: 100 } }}
              />
            </Box>
            <Lamp
              width="400px"
              bgColor="#7f4cf7"
              sx={{ top: { md: "30%", xs: "0%" } }}
            />
          </Parallax>
        </Box>

        <Stack
          justifyContent="center"
          alignItems="center"
          direction="column"
          width={{ md: "65%" }}
          sx={{
            mt: 10,
          }}
        >
          <Typography component="h2" variant="h2">
            <span
              css={css`
                font-weight: 500;
                text-transform: uppercase;
              `}
            >
              Nimbus Venture
            </span>{" "}
            Band
          </Typography>

          <Typography component="p" sx={{ mt: 1, maxWidth: "90%" }}>
            Body Temperature and Heart Beat tracker, Fall detection and Monitors
            heart rate in real-time
          </Typography>

          <Typography component="h6" variant="h6" sx={{ mt: 5 }}>
            Let the Journey begin!
          </Typography>

          <Stack
            direction="row"
            spacing={2}
            justifyContent="center"
            alignItems="center"
            sx={{ mt: 2 }}
          >
            {/* <Button variant="contained" size="large" sx={{background:"#14ff8a", color: '#000', flexGrow: 0}}>Get Started</Button> */}
            <IconButton color="primary" aria-label="play" size="small">
              <PlayCircleFilledIcon sx={{ fontSize: 70 }} />
            </IconButton>
          </Stack>
        </Stack>
      </Stack>
    </Container>
  );
};

export default Hero;
