import {
  BottomNavigation,
  Box,
  Button,
  ButtonGroup,
  Container,
  Grid,
  IconButton,
  Link,
  Paper,
  Stack,
  Typography,
} from "@mui/material";
import React from "react";
import Media from "../Media";
import logo from "../../assets/images/logo.png";

const Footer: React.FC = () => (
  <footer>
    <Paper elevation={2}>
      <Container maxWidth='lg' sx={{ pt: 12 }}>
        <Stack direction="column">
          <Stack
            spacing={10}
            direction={{ md: "row", xs: "column" }}
            sx={{ mb: 10 }}
          >
            <Box marginRight={{ md: "auto", xs: "unset" }}>
              <Media src={logo} alt="" sx={{ width: "200px", left: 0 }}></Media>
            </Box>
            <Box>
              <Typography component="h5" variant="h5" sx={{ mb: 4 }}>
                Products
              </Typography>
              <Stack spacing={2}>
                <Link underline="none">Fleet Management System</Link>
                <Link underline="none">Transport Management System</Link>
                <Link underline="none">MyCar</Link>
                <Link underline="none">Nimbus GPS Device</Link>
              </Stack>
            </Box>

            <Box>
              <Typography component="h5" variant="h5" sx={{ mb: 4 }}>
                Solutions
              </Typography>
              <Stack spacing={2}>
                <Link underline="none">Logistic Management</Link>
                <Link underline="none">Construction and Heavy Vehicles</Link>
                <Link underline="none">Retail Distribution</Link>
                <Link underline="none">Courier and Delivery service</Link>
              </Stack>
            </Box>
            <Box>
              <Typography component="h5" variant="h5" sx={{ mb: 4 }}>
                About Us
              </Typography>
              <Typography component="h5" variant="h5" sx={{ mb: 4 }}>
                Contact Us
              </Typography>
            </Box>
          </Stack>

          <Box marginBottom="auto">
            <Typography textAlign="center" component="p" sx={{ pb: 2 }}>
              &copy; 2023 Team Hack Elite+, All rights reserved.
            </Typography>
          </Box>
        </Stack>
      </Container>
    </Paper>
  </footer>
);

export default Footer;
